<template >
  <div :class="{ grid__box: grid, list__box: !grid }">
    <div class="card" v-for="(card, i) in cards" :key="i">
      <h2 class="card__title">{{ card.title }}</h2>
      <p class="card__time">{{ card.time }}</p>
      <p class="card__text">
        {{ card.text }}
      </p>
      <div class="card__btn" v-if="!search">
        <button class="card__btn-edit" @click="$emit('changeCard',card.id)">
          <img src="../assets/img/change.svg" alt="" />
          {{lagnWord.editbtn[lang]}}
        </button>
        <button class="card__btn-delete" @click="delCard(card.id)">
          <img src="../assets/img/delete.svg" alt="" />
        {{lagnWord.deledit[lang]}}
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    grid: Boolean,
    cards: Array,
    search:Boolean,
    lagnWord:Object,
    lang:String
  },
  methods:{
    delCard(id){
      this.cards.forEach((card,i)=>{
        if (card.id==id) {
          this.cards.splice(i,1)
        }
      })
    }
  }
};
</script>
