<template>
  <Nav
    :lang="lang"
    @change="lang = $event"
    :search="search"
    @changeNav="search = $event"
    @searchVal="searchInput = $event"
    :lagnWord="lagnWord"
  />
  <div class="container">
    <Grid
      :grid="grid"
      @changeGrid="grid = $event"
      :lagnWord="lagnWord"
      :lang="lang"
    />
    <Cards
      :grid="grid"
      :cards="cardFilter"
      @changeCard="changeCard"
      :search="search"
      :lagnWord="lagnWord"
      :lang="lang"
    />
  </div>
  <Transition name="modal">
    <Modal
      v-if="modal"
      @changeModal="modal = $event"
      @addCard="addCard"
      :add="add"
      :saveChange="saveChange"
      @saveCard="saveCard"
      :lagnWord="lagnWord"
      :lang="lang"
    />
  </Transition>

  <AddBtn v-if="!modal" @changeModal="modal = $event" v-show="!search" />
</template>

<script>
import Nav from "@/components/Nav.vue";
import Grid from "@/components/Grid.vue";
import Cards from "./components/Cards.vue";
import AddBtn from "./components/AddBtn.vue";
import Modal from "./components/Modal.vue";
import lang from "./lang";
export default {
  components: {
    Nav,
    Grid,
    Cards,
    AddBtn,
    Modal,
  },
  data() {
    return {
      lang: "uz",
      search: false,
      grid: true,
      modal: false,
      cards: [],
      id: 1,
      add: true,
      saveChange: {},
      searchInput: "",
      lagnWord: {},
    };
  },
  methods: {
    addCard(cardObj) {
      cardObj.id = this.id++;
      this.cards.push(cardObj);
      this.modal = false;
    },
    changeCard(cardId) {
      this.cards.forEach((card) => {
        if (cardId == card.id) {
          this.add = false;
          this.saveChange = {
            id: card.id,
            title: card.title,
            text: card.text,
          };
          this.modal = true;
        }
      });
    },
    saveCard(cardObj) {
      this.cards.forEach((card) => {
        if (cardObj.id == card.id) {
          (card.title = cardObj.title),
            (card.text = cardObj.text),
            (card.time = cardObj.time);
          this.modal = false;
        }
      });
    },
  },
  computed: {
    cardFilter() {
      let array = this.cards;
      let searchRes = this.searchInput;
      if (!this.search) return array;
      searchRes = searchRes.toLowerCase();
      array = array.filter((card) => {
        if (card.title.toLowerCase().indexOf(searchRes) != -1) return array;
      });
      return array;
    },
  },
  watch: {
    lang() {
      localStorage.setItem("lang", this.lang);
    },
    id() {
      localStorage.setItem("id", JSON.stringify(this.id));
    },
    cards: {
      handler(array) {
        localStorage.setItem("cards", JSON.stringify(array));
      },
      deep: true,
    },
  },
  created() {
    let localArr = localStorage.getItem("cards");
    let localId = localStorage.getItem("id");
    let localLang = localStorage.getItem("lang");
    this.lagnWord = lang;
    if (localArr) {
      this.cards = JSON.parse(localArr);
    }
    if (localId) {
      this.id = JSON.parse(localId);
    }
    if (localLang) {
      this.lang = localLang;
    }
  },
};
</script>
<style lang="scss">
.modal-enter-active,
.modal-leave-active {
  transition: all 0.5s;
}
.modal-enter-from,
.modal-leave-to {
  opacity: 0;
  transform: scale(1.5);
}
</style>


