<template >
  <div class="grid">
    <h2 class="grid__title">{{lagnWord.infobar[lang]}}</h2>
    <button class="grid__btn" @click="$emit('changeGrid', !grid)">
      <img src="../assets/img/grid.svg" alt="" v-if="grid" />
      <img src="../assets/img/list.svg" alt="" v-else />
      <span v-if="grid">{{lagnWord.grid[lang]}}</span>
      <span v-else>{{lagnWord.list[lang]}}</span>
    </button>
  </div>
</template>
<script>
export default {
  props: {
    grid: Boolean,
    lagnWord:Object,
    lang:String
  },
};
</script>
